.App {
  background-color: #282c34;
  text-align: center;
  overflow: hidden;
}

.App-header {
  padding-top: 10px;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

nav {
  margin: 0 auto;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  width: 90vw;
}

nav button {
  font-size: 16px;
  padding: 10px 7px;
  background-color: grey;
  border: solid 1px black;
}
