* {
  font-family: Arial;
  box-sizing: border-box;
  color: black;
}

table {
  margin: 5px auto;
  width: 90vw;
}
tr {
  background-color: grey;
}

th {
  background-color: #585151;
}

tr:nth-child(2n) {
  background-color: #afa9a9;
}

.product-name {
  display: flex;
  justify-content: space-between;
  padding: 10px 5px;
  font-size: 16px;
}

select,
input {
  height: 40px;
  font-size: 16px;
  width: 20vw;
  text-align: center;
}

.submit {
  width: 200px;
  padding: 5px;
  margin: 10px;
  font-size: 20px;
  border: none;
  text-transform: uppercase;
}

.fas {
  color: rgb(54, 51, 51);
  font-size: 28px;
  background-color: none;
}
